import manipulate from './manipulate.vue'
import LegalBuy from './LeagalBuy.vue'
import LegaSell from './LeagalSell.vue'
export default {
	name:'LegalCoin',
	props: {
		type:null
	},
	data() {
		return {
			activeIndex:0,
			coinType:0,
			list: {
				coinType:0,
				current:0
			}
		}
	},
	components: {
		manipulate,
		LegalBuy,
		LegaSell
	},
	methods:{
		activeUpdate(index) {
			this.activeIndex = index;
			this.list.current = index;
			/* this.$set(this.list, 'current', 2) */
		}
	},
	watch: {
		/* type(val) {
			if(val === 0) {
				this.coinType = 0
			}else if(val === 1) {
				this.coinType =  1
			}
		} */
		type(val) {
			if(val === 0) {
				this.list.coinType = 0
			}else if(val === 1) {
				this.list.coinType =  1
			}
		}
	}
}
